import React from 'react'
import { Container, Typography, Button, Grid } from '@material-ui/core'
import useStyle from './styles'
import CartItem from './CartItem/CartItem'
import {Link} from 'react-router-dom'


const Cart = ({ cart, onUpdateCartQty, onRemoveFromCart, onEmptyCart}) => {
    const classes = useStyle();
    const EmptyCart = () => (
        <Typography variant="subtitle1">You have no item in your shopping cart, start adding some,
        <Link to="/" className={classes.link}>Add some items to you shopping cart</Link>
        </Typography>
    );

    const FilledCart = () => (
        <>
        <Grid container spacing={3}>
            {cart.line_items.map((item) => (
                <Grid item xs={12} sm={4} key={item.id}>
                    <CartItem item={item} onUpdateCartQty={onUpdateCartQty} onRemoveFromCart={onRemoveFromCart} />
                </Grid>
            ))}
        </Grid>
        <div className={classes.cardDetails}>
            <Typography variant="h5">
                Subtotal: {cart.subtotal.formatted_with_code}
            </Typography>
            <div>
                <Button className={classes.emptyButton} size='large' type="button" variant="contained" color="secondary" onClick={onEmptyCart}>Empty Cart</Button>
                <Button component={Link} to="/Checkout" className={classes.checkoutButton} size='large' type="button" variant="contained" color="primary">Checkout</Button>

            </div>
        </div>
        </>
    )
    if (!cart.line_items) return '...loading';
    
    return (
        <Container>
            <div className={classes.toolbar} />
            <Typography className={classes.title} variant="h3" gutterBottom>Your Shopping Cart</Typography>
            {!cart.line_items.length ? <EmptyCart /> : <FilledCart />}
        </Container>
    )
}

export default Cart
